}
.popup-consultation .title {
  color: #353535;
}
.popup-consultation__close {
  cursor: pointer;
}
.popup-consultation form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
}
.popup-consultation .contact__input {
  flex-basis: 100%;
}
.popup-consultation__input {
  position: relative;
  flex-basis: 100%;
  height: 65px;
  margin-bottom: 30px;
}
.popup-consultation input,
.popup-consultation textarea {
  width: 100%;
