  font-size: 22px;
  color: #D61322;
  padding-bottom: 13px;
  border: 0;
  border-bottom: 3px solid #AB0F1B;
}
.popup-consultation textarea {
  height: 70%;
}
.popup-consultation label {
  position: absolute;
  left: 0;
  bottom: 0;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #353535;
}
.popup-consultation .button {
  max-width: 320px;
  margin: 0 auto;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #D61322;
  background: #fff;
  padding: 16px 36px;
  border: 1px solid #D61322;
  cursor: pointer;
  transition: all ease 0.5s;
}
.popup-consultation .button:hover {
  background: #D61322;
  color: #fff;
}
.popup-consultation p {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .popup-consultation__body {
    height: 100%;
  }
  .popup-consultation__content {
    height: 100%;
    overflow: scroll;
  }
  .popup-consultation__input {
    flex-basis: 100%;
  }
}

